import TableRow from "./TableRow";
import Sticky from "react-stickynode";

export default function LeagueTable({ teams, showGoals }) {
  return (
    <div className="table">
      <div className="row head-row">
        <th>拽</th>
        <th>拽爪</th>
        <th>砖拽</th>
        {showGoals && <th>驻专砖</th>}
        <th>拽转</th>
      </div>
      <Sticky enabled={true} top={0} bottomBoundary={1200}>
        <div className="top-4">
          {teams.slice(0, 4).map((t, i) => (
            <TableRow team={t} showGoals={showGoals} idx={i} />
          ))}
        </div>
      </Sticky>
      <div>
        {teams.slice(4).map((t, i) => (
          <TableRow team={t} showGoals={showGoals} idx={i + 4} />
        ))}
      </div>
    </div>
  );
}
