export default function TableRow({ team = {}, showGoals = false, idx }) {
  return (
    <div className={"row"} key={team.key}>
      <div className="col">{idx + 1}</div>
      <div className="col">{team.displayName}</div>
      <div className={`col ${team.games === 37 ? "done" : ""} `}>
        {team.games}
      </div>
      {showGoals && <div className="col gf">{team.goalDiffrence}</div>}
      <div className="col">{team.points}</div>
    </div>
  );
}
